import React, { Component } from 'react';
import { Card, CardHeader, Col, Row, Button, Form, Input, FormGroup, Label, Modal, ModalHeader, ModalBody, ModalFooter } from 'reactstrap';
import { toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import commonService from '../../../core/services/commonService';

import Loader from '../../Loader/Loader';
import { FormErrors } from '../../Formerrors/Formerrors';
import SubCategoryData from './SubCategoryData';
import './SubCategory.css'


class SubCategory extends Component {
  constructor(props){
    super(props);
    this.state = {
      modal: false,
      subCategory_name: "",
      categoryId: "",
      subCategoryList: [],
      subCategoryImage: null,
      subCategoryImageUrl: "",
      loading: true,
      status: "",
      rowIndex: -1,
      remark: "",
      sponsorId: "",
      categoryList: [],
      sponsorsList: [],
      formErrors: {subCategory_name: '', subCategory_image: '', category: '', error: ''},
      errors: {},
      subCategory_name_valid: false,
      filterItem: {},
      category_valid: false,
      formValid: false,
      formProccessing: false,

    } 
    this.handleEditSubCategory = this.handleEditSubCategory.bind(this);
    this.submitHandler = this.submitHandler.bind(this);
    this.handleDeleteSubCategory = this.handleDeleteSubCategory.bind(this);
    this.filterItemList = this.filterItemList.bind(this);
    this.resetSearchFilter = this.resetSearchFilter.bind(this);
  }

  // Fetch the subCategory List
  componentDidMount() { 
    this.subCategoryList();
    this.categoryList();
    this.sponsorsList();
  }

  /*categoryList List API*/
  categoryList() {   
   
    commonService.getAPIWithAccessToken('category')
      .then( res => {
        
         
        if ( undefined === res.data.data || !res.data.status ) {
          this.setState( {  loading: false } );
          toast.error(res.data.message);    
          return;
        }   

        this.setState({categoryList: res.data.data});     
       
      } )
      .catch( err => {         
        if(err.response !== undefined && err.response.status === 401) {
          localStorage.clear();
          this.props.history.push('/login');
        }
        else           
          toast.error(err.message);    
        
      } )
    
  }
  /*subCategory List API*/
  subCategoryList(filterItem = {}) {
    let filterParmas = "";
    
    
    if(filterItem.custom !== undefined && filterItem.custom !== "" ) 
      filterParmas += (filterParmas !=="" ) ? "&keyword="+filterItem.custom: "?keyword="+filterItem.custom;
    if(filterItem.sponsorId !== undefined && filterItem.sponsorId !== "" ) 
      filterParmas += (filterParmas !=="" ) ? "&sponsorId="+filterItem.sponsorId: "?sponsorId="+filterItem.sponsorId;
    if(filterItem.categoryId !== undefined && filterItem.categoryId !== "" ) 
      filterParmas += (filterParmas !=="" ) ? "&categoryId="+filterItem.categoryId: "?categoryId="+filterItem.categoryId;
    if(filterItem.status !== undefined && filterItem.status !== "" ) {
      let statusText = commonService.getStatusVal(filterItem.status)
      filterParmas += (filterParmas !=="" ) ? "&status="+statusText: "?status="+statusText;
    }
    this.setState( { loading: true}, () => {
      commonService.getAPIWithAccessToken('sub-category'+filterParmas)
        .then( res => {
          
           
          if ( undefined === res.data.data || !res.data.status ) {
            this.setState( {  loading: false } );
            toast.error(res.data.message);    
            return;
          }   

          this.setState({loading:false, subCategoryList: res.data.data});     
         
        } )
        .catch( err => {         
          if(err.response !== undefined && err.response.status === 401) {
            localStorage.clear();
            this.props.history.push('/login');
          }
          else {
            this.setState( { loading: false } );
            toast.error(err.message);    
          }
        } )
    } )
  }
  /*sponsorsList List API*/
  sponsorsList(){
    commonService.getAPIWithAccessToken('sponsors')
      .then( res => {
        
         
        if ( undefined === res.data.data || !res.data.status ) {
          this.setState( {  loading: false } );
          toast.error(res.data.message);    
          return;
        }   

        this.setState({sponsorsList: res.data.data});     
       
      } )
      .catch( err => {         
        if(err.response !== undefined && err.response.status === 401) {
          localStorage.clear();
          this.props.history.push('/login');
        }
        else           
          toast.error(err.message);    
        
      } )
  }

  submitHandler (event) {
    event.preventDefault();
    event.target.className += " was-validated";
    if(!this.validateForm())
      return false;
    let formData = {}; 
    formData.categoryId = this.state.categoryId;
    formData.subCategoryName = this.state.subCategory_name;
    formData.sponsorId = this.state.sponsorId;
    let statusText = commonService.getStatusVal(this.state.status);
    formData.status = statusText;
    formData.remark = this.state.remark;
    
    this.setState( { formProccessing: true, loading: true}, () => {
      if(this.state.rowIndex > -1){
        /* Update subCategory */
        const subCategoryId = this.state.subCategoryList[this.state.rowIndex].subCategoryId;
        
        formData.subCategoryId = subCategoryId;
        commonService.putAPIWithAccessToken( `sub-category`, formData)
          .then( res => {
            
           
            
            if ( undefined === res.data.data || !res.data.status ) { 
              let errors = {};
              if(res.data.errors !== undefined)
                errors = res.data.errors;
              else
                errors.categoryName = res.data.message;
              this.setState( { formProccessing: false, loading: false, errors: errors} );
              return false;
            }
            let updatedsubCategoryLists = this.state.subCategoryList;
            updatedsubCategoryLists[this.state.rowIndex] = res.data.data;
            this.setState({modal: false, rowIndex: "", formProccessing: false, loading: false});
            toast.success(res.data.message);    
            this.subCategoryList();
          } )
          .catch( err => {       
              
            if(err.response !== undefined && err.response.status === 401) {
              localStorage.clear();
              this.props.history.push('/login');
            }
            else {
              this.setState( { formProccessing: false, loading: false } );
              toast.error(err.message);    
            }
          } )
       
      }
      else{
         /* Add subCategory */
        commonService.postAPIWithAccessToken( `sub-category`, formData)
          .then( res => {
            
           
            if ( undefined === res.data.data || !res.data.status ) { 
              let errors = {};
              if(res.data.errors !== undefined)
                errors = res.data.errors;
              else
                errors.categoryName = res.data.message;
              this.setState( { formProccessing: false, loading: false, errors: errors} );
              return false;
            }           
            this.setState({modal: false, formProccessing: false, loading: false});
            toast.success(res.data.message); 
            this.subCategoryList();
          } )
          .catch( err => {       
              
            if(err.response !== undefined && err.response.status === 401) {
              localStorage.clear();
              this.props.history.push('/login');
            }
            else{
              this.setState( { formProccessing: false, loading: false } );
              toast.error(err.message); 
            }
          } )
      }
    })
  };
  /* Input Field On changes*/
  changeHandler = event => {
    const name = event.target.name;
    const value = event.target.value
    this.setState({ [name]: value });
  };
  
  
  /* Validate Form */
  validateForm() {
    let formValid = true;
    let formField = this.state; 
    let errors = {};
    if(formField.categoryId === undefined || formField.categoryId.trim() === "" ){
      formValid = false;
      errors['categoryName'] = 'Study field required';
    }
    if(formField.subCategory_name === undefined || formField.subCategory_name.trim() === "" ){
      formValid = false;
      errors['subCategoryName'] = 'Sub Study field required';
    }
    if(formField.status === undefined || formField.status.trim() === "" ){
      formValid = false;
      errors['status'] = 'Status field required';
    }
    this.setState({errors:  errors});
    return formValid;
    
  }
  /* Set Error Class*/
  errorClass(error) {
    return(error.length === 0 ? '' : 'has-error');
  }

  toggle = () => {
    this.setState({
      modal: !this.state.modal,
      subCategory_name: "",
      categoryId: "",
      rowIndex: -1,
      formValid: false,
      subCategoryImageUrl: ""
    });
  }
  

  /* Edit subCategory*/
  handleEditSubCategory(rowIndex){
    
      const subCategoryItem = this.state.subCategoryList[rowIndex];
      let status = commonService.getStatus(subCategoryItem.status);
      this.setState({modal: true, status: status, subCategory_name: subCategoryItem.subCategoryName, remark: subCategoryItem.remark, sponsorId: subCategoryItem.sponsorId, subCategory_name_valid: true, category_valid: true, rowIndex: rowIndex, categoryId:subCategoryItem.categoryId, subCategoryImageUrl:subCategoryItem.imagUrl, formValid: true});
  }
  /* Add subCategory */
  handleDeleteSubCategory(rowIndex){
   
    const subCategoryItem = this.state.subCategoryList[rowIndex];
   
    this.setState( { loading: true}, () => {
      commonService.deleteAPIWithAccessToken( `sub-category/`+subCategoryItem.subCategoryId)
        .then( res => {
          this.setState({loading: false});
          if ( undefined === res.data || !res.data.status ) {            
             toast.error(res.data.message);      
            return;
          }         
          
          toast.success(res.data.message);
          this.setState({filterItem: {}});
          this.subCategoryList();
        } )
        .catch( err => {       
            
          if(err.response !== undefined && err.response.status === 401) {
            localStorage.clear();
            this.props.history.push('/login');
          }
          else{
            this.setState( { loading: false } );
            toast.error(err.message);
          }
      } )
    })
  }

  filterItemList(){
    const filterItem = this.state.filterItem;
    this.subCategoryList(filterItem);
  }
  
  changeFilterHandler = event => {
    const name = event.target.name;
    const value = event.target.value;
    const filterItem = this.state.filterItem
    filterItem[name] = value;
    this.setState({ filterItem: filterItem });
  };
  resetSearchFilter() {
    this.setState({filterItem: { status: '', custom: '', categoryId: "", sponsorId: ""}});
    this.subCategoryList();
  }

  render() {

    const { subCategoryList, loading, modal, categoryList, categoryId, formProccessing, errors  } = this.state;     
    let loaderElement ='';
    if(loading)
      loaderElement = <Loader />
    /*let categoryImagePreview = '';
    if(this.state.subCategoryImageUrl !== "")
      categoryImagePreview = <div className="imagePreview"><img src={this.state.subCategoryImageUrl} alt="" /></div>*/
    const processingBtnText = <>Submit <i className="fa fa-spinner"></i></>;

    return (
      <div className="animated fadeIn">
        <Row>
          {loaderElement}
          <Col lg={12}>
            <Card className="oep-card ">
                <CardHeader className="mainHeading">
                  <strong>Sub Studies List</strong> 
                  <Button color="" className="categoryAdd" type="button" onClick={this.toggle}><i className="fa fa-plus"></i> Add Sub Study</Button>
                </CardHeader>
                <Row>
                  <Col md={12}>
                    <div className="search-filter">
                      <Row>                      
                        
                        <Col md={"2"}>
                          <FormGroup> 
                            <Label htmlFor="categoryId">Search By Study</Label>            
                            <Input type="select" placeholder="Study Name *" name="categoryId" value={this.state.filterItem.categoryId} onChange={this.changeFilterHandler} >
                              <option value="">Select</option>
                              {categoryList.map((categoryItem, index) =>
                                <SetCategoryDropDownItem key={index} categoryItem={categoryItem} selectedCategory={categoryId} />
                              )}
                            </Input>
                          </FormGroup>  
                        </Col>
                        <Col md={"2"}>
                          <FormGroup> 
                            <Label htmlFor="sponsorId">Search By Sponsor</Label>            
                            <Input type="select" placeholder="Select Sponsor" id="sponsorId" name="sponsorId" value={this.state.filterItem.sponsorId} onChange={this.changeFilterHandler} >
                              <option value="">Select Sponsor</option>
                              {this.state.sponsorsList.map((categoryItem, index) =>
                                <SetCategoryDropDownItem key={index} categoryItem={{categoryId: categoryItem.sponsorId, categoryName: categoryItem.companyName}}  />
                              )}
                            </Input>  
                          </FormGroup>  
                        </Col>
                        <Col md={"3"}>
                          <FormGroup> 
                            <Label htmlFor="filter_doctor_id">Search By Name</Label>            
                            <Input type="text" placeholder="Search By Name" id="custom" name="custom" value={this.state.filterItem.custom} onChange={this.changeFilterHandler} />
                          </FormGroup>  
                        </Col>
                        <Col md={"2"}>
                          <FormGroup> 
                            <Label htmlFor="filter_doctor_id">Search By Status</Label>            
                            <Input type="select" placeholder="Search By Status" id="status" name="status" value={this.state.filterItem.status} onChange={this.changeFilterHandler}>
                                <option value = "">Select</option>
                                <option value="Active">Active</option>
                                <option value="Inactive">Inactive</option>
                                <option value="Pending">Pending</option>
                                <option value="Under Negotiation">Under Negotiation</option>
                            </Input>
                          </FormGroup>  
                        </Col>
                        <Col md={"3"}>
                          <FormGroup className="filter-button-section"> 
                            <Button className="search-btn" type="button" onClick={this.filterItemList}>Search</Button> 
                            <Button className="search-btn" id="resetButton" type="button" onClick={this.resetSearchFilter}>Reset</Button> 
                          </FormGroup>             
                        </Col>
                      </Row>
                    </div>  
                  </Col>       
                  <Col lg= {12}> 
                    <SubCategoryData data={subCategoryList} toggle={this.toggle} editSubCategoryAction={this.handleEditSubCategory} deleteSubCategoryAction={this.handleDeleteSubCategory} dataTableLoadingStatus = {this.state.loading} />
                  </Col>
                </Row> 
            </Card>
          </Col>
        </Row>
        <Modal isOpen={modal} toggle={this.toggle} className="subCategory-modal-section">
          <ModalHeader toggle={this.toggle}>Sub Study</ModalHeader>
          <Form onSubmit={this.submitHandler} noValidate>
            <ModalBody>
              <FormErrors formErrors={this.state.formErrors} />
              <FormGroup> 
                <Label htmlFor="categoryId">Study <span className="mandatory">*</span></Label>            
                <Input type="select" placeholder="Studies Name *" invalid={errors['categoryName'] !== undefined && errors['categoryName'] !== ""} id="categoryId" name="categoryId" value={this.state.categoryId} onChange={this.changeHandler} required >
                  <option value="">Select</option>
                  {categoryList.map((categoryItem, index) =>
                    <SetCategoryDropDownItem key={index} categoryItem={categoryItem} selectedCategory={categoryId} />
                  )}
                </Input>
                <div className="invalid-feedback">
                  {errors['categoryName']}
                </div>
              </FormGroup>
              <FormGroup> 
                <Label htmlFor="sponsorId">Select Sponsor </Label>            
                <Input type="select" placeholder="Select Sponsor" id="sponsorId" name="sponsorId" value={this.state.sponsorId} onChange={this.changeHandler} >
                  <option value="">Select Sponsor</option>
                  {this.state.sponsorsList.map((categoryItem, index) =>
                    <SetCategoryDropDownItem key={index} categoryItem={{categoryId: categoryItem.sponsorId, categoryName: categoryItem.companyName}}  />
                  )}
                </Input>
              </FormGroup>
              <FormGroup> 
                <Label htmlFor="subCategory_name">Sub Study <span className="mandatory">*</span></Label>            
                <Input type="text" placeholder="Sub Studies *" invalid={errors['subCategoryName'] !== undefined && errors['subCategoryName'] !== ""} id="subCategory_name" name="subCategory_name" value={this.state.subCategory_name} onChange={this.changeHandler} required />
                <div className="invalid-feedback">
                  {errors['subCategoryName']}
                </div>
              </FormGroup>
              <FormGroup> 
                <Label htmlFor="category_name">Remark</Label>            
                <Input type="textarea" placeholder="Remark " id="remark" name="remark" value={this.state.remark} onChange={this.changeHandler} />
              </FormGroup>
              <FormGroup>
                <Label htmlFor="template_status">Status</Label>            
                <Input type="select" placeholder="Status *" invalid={errors['status'] !== undefined && errors['status'] !== ""} id="status" name="status" value={this.state.status} onChange={this.changeHandler} required >
                  <option value="">Select Status</option>
                  <option value="Active">Active</option>
                  <option value="Inactive">Inactive</option>
                  <option value="Pending">Pending</option>
                  <option value="Under Negotiation">Under Negotiation</option>
                </Input>
                <div className="invalid-feedback">
                  {errors['status']}
                </div>
              </FormGroup>
             
            </ModalBody>
            <ModalFooter>
              <Button color="primary" disabled={formProccessing} type="submit">{formProccessing ? processingBtnText : 'Submit' }</Button>
              <Button color="secondary" onClick={this.toggle}>Cancel</Button>
            </ModalFooter>
          </Form>
        </Modal>
      </div>

    )
  }
}

function SetCategoryDropDownItem (props) {
  const categoryItem = props.categoryItem;
  return (<option value={categoryItem.categoryId} >{categoryItem.categoryName}</option>)
}

export default SubCategory;
