import React, { Component } from 'react';
//import { Button } from 'reactstrap';
import MUIDataTable from "mui-datatables";
import commonService from '../../../core/services/commonService';

class SubCategoryData extends Component {
  
  constructor(props){
    super(props);   
    this.state = {
      buttonProcessing: false,
      rowIndex: '',
      dataTableItem: []
    };
    
  }
  componentDidMount() {   
  }
  /* Edit SubCategory Item */
  editSubCategoryItem(rowIndex){    
    this.props.editSubCategoryAction(rowIndex);
  }
  /*Delete SubCategory*/
  deleteSubCategoryItem(rowIndex){    
    this.props.deleteSubCategoryAction(rowIndex);
  }
  
  render() {
    
    let rowsItem = [];    
    for(const [i, cat] of this.props.data.entries()){
      console.log(i);
      let catInfo = {
        name: cat.subCategoryName,
        image: cat.imagUrl,
        parentCategory: cat.categoryName,
        status: commonService.getStatus(cat.status),
        remark: cat.remark || "",
        sponsorName: cat.sponsorName || "",       
        action: "",       
      }      
      rowsItem.push(catInfo);
    }    
    const columns = [
       /*{
        label: 'Image',
        name: 'image',
        options: {
          filter: true,
          customBodyRender: (value, tableMeta, updateValue) => {
            return (
             <p className="imagePreview"><img src={value} alt="" /></p>
            );
          },
        }
      },*/
      {
        label: 'Sub Study',
        name: 'name',
      },
      {
        label: 'Study',
        name: 'parentCategory'
      },
      {
        label: 'Remark',
        name: 'remark'
      },
      {
        label: 'Sponsor Name',
        name: 'sponsorName'
      },
      {
        label: 'Status',
        name: 'status',
      },
      {
        label: 'Action',
        name: 'action',
        options: {
          filter: true,
          customBodyRender: (value, tableMeta, updateValue) => {
            let i = tableMeta.rowIndex;
           
            return (
             <p><a href="#!"className="btn-edit" disabled={this.state.buttonProcessing} onClick={() => 
              this.editSubCategoryItem(i)}><i className="fa fa-pencil"></i> </a>
              <a href="#!" className="btn-delete" disabled={this.state.buttonProcessing} onClick={() => 
              {if( window.confirm('Are you sure you wish to delete this sub practice?')) this.deleteSubCategoryItem(i)}}><i className="fa fa-trash"></i></a></p>
            );
          },
        }
      },
    ];
    const options = {
      search: false,
      viewColumns: false,
      filter: false,
      searchOpen: false,
      print: false,
      download: false,
      responsive: 'stacked',
      selectableRows: 'none',
      textLabels: {
        body: {
          noMatch: this.props.dataTableLoadingStatus ? "Processing........" : "",
          toolTip: "Sort",
          columnHeaderTooltip: column => `Sort for ${column.label}`
        },
      },
      
      

    };
    return (
      <MUIDataTable
        
        data={rowsItem}
        columns={columns}
        options={options}
      />
    );
  }
}

export default SubCategoryData;